.page-header {
  width: 100%;
  margin: 0 auto;
}

.page-header__version-toggle {
  background-color: $blue;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;

  p {
    color: $white;
  }
}

.page-header__version-toggle-icon {
  width: 32px;
  margin: 7px 10px 0 0;

  svg {
    width: 32px;
    height: 32px;
  }
}

.main-nav {
  width: 100%;
  max-width: 1212px;
  margin: 0 auto;
  padding: 28px 0;
  display: flex;
  justify-content: space-between;
}

.main-nav-menu {
  width: 100%;
  max-width: 1212px;
  margin: 0 auto;
  padding: 20px 0;
  display: flex;
  justify-content: space-between;
}

.main-nav__menu,
.main-nav__help-list,
.main-nav__help-item {
  display: flex;
  justify-content: left;
  align-items: center;
}

.main-nav__logo {
  width: 234px;
  text-decoration: none;
  margin: 4px 64px 0 0;

  svg {
    width: 234px;
    height: 28px;
  }
}

.main-nav__toggle {
  width: 32px;
  height: 32px;
  background-image: url("../img/menu-icon.svg");
  background-color: transparent;
  border: none;
  margin: 0 33px 0 0;
  cursor: pointer;

  &--close {
    background-image: url("../img/menu-close-icon.svg");
  }
}

.main-nav__city {
  color: $blue;
  text-decoration: none;
}

.main-nav__help-list {
  width: 504px;
  margin: 0;
  padding: 0;
  justify-content: flex-end;
}

.main-nav__help-item {
  text-align: left;

  a {
    text-decoration: none;
  }

  &:first-child {
    margin: 0 27px 0 0;
  }

  &:nth-child(2) {
    margin: 0 24px 0 0;
  }

  &:nth-child(3) {
    margin: 0 26px 0 0;

    a {
      color: $blue;
      width: 104px;
    }
  }

  &:nth-child(4) {
    a {
      color: $blue;
      width: 94px;
    }
  }

  &:last-child {
    margin: 0;
  }
}

.main-nav__help-item-description {
  &:first-of-type {
    width: 55px;
  }

  width: 92px;
  margin: 0;
  color: $blue;
}

.main-nav__help-item-button {
  color: $white;
  width: 230px;
}

.main-nav__help-item-icon {
  width: 32px;
  margin: 3px 10px 0 0;

  svg {
    width: 32px;
    height: 32px;
  }
}

@media (max-width: $tablet-max-width) {
  .page-header__version-toggle {
    p {
      margin: 10px 0;
      font-weight: bold;
      font-size: 14px;
      line-height: 20px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
    }
  }

  .page-header__version-toggle-icon {
    width: 24px;
    margin: 4px 8px 0 0;

    svg {
      width: 24px;
      height: 24px;
    }

  }

  .main-nav {
    padding: 16px 26px 0 32px;
    box-sizing: border-box;
  }

  .main-nav-menu {
    width: 100%;
    max-width: 1212px;
    margin: 0 auto;
    padding: 16px 26px 0 32px;
    display: block;
  }

  .main-nav__logo {
    width: 165px;
    text-decoration: none;
    margin: 4px 64px 0 0;

    svg {
      width: 165px;
      height: 20px;
    }
  }

  .main-nav__toggle {
    width: 30px;
    height: 30px;
    margin: 0 22px 0 0;

    &--close {
      margin: -8px 22px 0 -3px;
      background-image: url("../img/menu-close-icon.svg");
    }
  }

  .main-nav__city {
    display: none;
  }

  .main-nav__help-list {
    width: 117px;

    &--menu {
      margin: 32px 0 0;
      width: 305px;
      justify-content: space-between;
    }
  }

  .main-nav__help-item {
    text-align: left;

    &:nth-child(2) {
      margin: 0;
    }

    &:nth-child(3) {
      display: none;
    }

    &:nth-child(4) {
      display: none;
    }

    &:last-child {
      display: none;
    }
  }

  .main-nav__help-item-icon {
    width: 24px;
    margin: 3px 8px 0 0;

    svg {
      width: 24px;
      height: 24px;
    }

    &--big {
      width: 32px;
      margin: 3px 10px 0 0;

      svg {
        width: 32px;
        height: 32px;
      }

    }
  }

  .main-nav__help-item-description {
    width: 83px;
    font-size: 12px;
    line-height: 14px;

    &--menu {
      width: 150px;
      font-size: 16px;
      line-height: 19px;
    }
  }
}

@media (max-width: $mobile-max-width) {
  .main-nav {
    padding: 16px 24px 0 24px;
  }

  .main-nav__logo {
    width: 99px;

    margin: 4px 37px 0 0;

    svg {
      width: 99px;
      height: 12px;
    }
  }

  .main-nav__toggle {
    margin: 0 12px 0 0;
  }

  .main-nav__help-list {
    width: 107px;

    &--menu {
      margin: 32px 0 0;
      width: 272px;
      display: block;
    }
  }

  .main-nav__help-item {
    text-align: left;

    &:first-child {
      margin: 0 0 10px 0;
    }

    &:nth-child(2) {
      margin: 0;
    }
  }

  .main-nav__help-item-description {

  }

  .main-nav__help-item-icon {
    width: 24px;
    margin: 3px 8px 0 0;

    svg {
      width: 24px;
      height: 24px;
    }

    &--big {
      width: 32px;
      margin: 3px 10px 0 0;

      svg {
        width: 32px;
        height: 32px;
      }

    }
  }

  .main-nav__help-item-description {
    width: 83px;

    &:first-of-type {
      width: 55px;
    }

    &--menu {
      width: 200px;

      &:first-of-type {
        width: 200px;
      }
    }
  }
}
