.oms {
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 65px 0 108px;
}

.oms-about {
  margin: 0 auto;
  width: 100%;
  max-width: 1212px;
  text-align: left;
  position: relative;

  h1 {
    margin: 0;
    font-family: $Stratos;
    font-weight: normal;
    font-size: 48px;
    line-height: 58px;
    color: $blue;
  }
}

.oms-about__list-wrapper {
  width: 1296px;
  position: relative;

  &::after {
    position: absolute;
    right: -4px;
    top: 0;
    display: inline-block;
    content: "";
    width: 312px;
    height: 380px;
    background-image: url("../img/card-mask.png");
    background-repeat: no-repeat;
    background-position: right;
  }
}

.oms-about__list {
  overflow: auto;
  width: 1296px;
  list-style: none;
  margin: 48px 0 0 3px;
  padding: 0;
  display: flex;
  justify-content: left;
  position: relative;
  scrollbar-width: none;
  -ms-overflow-style: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.oms-about__item {
  min-width: 312px;
  height: 380px;
  margin: 0 16px 0 0;
  box-sizing: border-box;
  padding: 32px 30px 0 24px;
  border-radius: 8px;
  cursor: pointer;

  p {
    font-family: $Stratos;
    font-size: 24px;
    line-height: 29px;
    color: $white;
    margin: 0;
  }

  &--first {
    background-image: url("../img/oms_card-1-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-1-desktop@2x.jpg");
    }
  }

  &--second {
    background-image: url("../img/oms_card-2-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-2-desktop@2x.jpg");
    }
  }

  &--third {
    background-image: url("../img/oms_card-3-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-3-desktop@2x.jpg");
    }
  }

  &--fourth {
    background-image: url("../img/oms_card-4-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    @media (min-resolution: $retina-dppx) {
      background-image: url("../img/oms_card-4-desktop@2x.jpg");
    }
  }

  &:last-child {
    margin: 0;
  }

}

.oms-about__list-prev,
.oms-about__list-next {
  position: absolute;
  width: 48px;
  height: 48px;
  background-repeat: no-repeat;
  background-size: 48px 48px;
  top: 272px;
  cursor: pointer;
}

.oms-about__list-prev {
  left: -62px;
  background-image: url("../img/arrow_left-icon.svg");
}

.oms-about__list-next {
  right: -62px;
  background-image: url("../img/arrow_right-icon.svg");
}

@media (max-width: $tablet-max-width) {
  .oms {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 36px 0 68px 31px;
  }

  .oms-about {
    margin: 0 auto;
    width: 100%;
    max-width: 1212px;
    text-align: left;

    h1 {
      font-size: 24px;
      line-height: 24px;
    }
  }

  .oms-about__list-wrapper {
    width: 100%;
    max-width: 1249px;
    &::after {
      display: none;
    }
  }

  .oms-about__list {
    width: 100%;
    max-width: 1249px;
    margin: 30px 0 0 0;
  }

  .oms-about__list-prev,
  .oms-about__list-next {
    display: none;
  }

  .oms-about__list-pagination {

  }
}

@media (max-width: $mobile-max-width) {
  .oms {
    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    padding: 36px 0 68px 23px;
  }

  .oms-about__list-wrapper {
    max-width: 608px;
  }

  .oms-about__list {
    max-width: 608px;
    margin: 18px 0 0 0;
  }

  .oms-about__item {
    min-width: 225px;
    height: 274px;

    &--first {
      background-image: url("../img/oms_card-1-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-1-mobile@2x.jpg");
      }
    }

    &--second {
      background-image: url("../img/oms_card-2-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-2-mobile@2x.jpg");
      }
    }

    &--third {
      background-image: url("../img/oms_card-3-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-3-mobile@2x.jpg");
      }
    }

    &--fourth {
      background-image: url("../img/oms_card-4-mobile@1x.jpg");
      @media (min-resolution: $retina-dppx) {
        background-image: url("../img/oms_card-4-mobile@2x.jpg");
      }
    }
  }
}
