.info {
  width: 100%;
  max-width: 1212px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-start;
  text-align: left;
  padding: 0 0 77px;

  h3 {
    font-family: $Stratos;
    font-weight: normal;
    font-size: 24px;
    line-height: 29px;
    color: $blue;
    margin: 0;
  }

  p {
    margin: 16px 0 0;
  }
}

.info__bonuses {
  width: 285px;
  margin: 0 125px 0 0;

  p {
    width: 250px;
  }

}

.info__bonuses-form {
  margin: 22px 0 0;
  position: relative;

  input {
    width: 285px;
    height: 56px;
    border: 1px solid $button-second;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 26px 0 10px 16px;
  }

  & input ~ label {
    position: absolute;
    top: 18px;
    left: 16px;
    pointer-events: none;
    transition: 0.3s;
  }

  & input:focus ~ label,
  & input:not(:focus):valid ~ label {
    top: 8px;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.02em;
  }
}

.info__bonuses-button {
  margin: 16px 0 0 0;
  width: 100%;
  letter-spacing: 0.03em;
  background-color: $button-second;
  color: $blue;
}

.info__knowledge {
  width: 320px;
  margin: 0 93px 0 0;
}

.info__knowledge-list,
.iinfo__news-list {
  list-style: none;
  margin: 16px 0 0 2px;
  padding: 0;
}

.info__knowledge-item {
  margin: 0 0 16px;
  width: 270px;

  a {
    text-decoration: none;
    color: $black-text;;
  }
}

.info__news {
  width: 388px;
}

.info__news-item {
  display: flex;
  justify-content: flex-start;
  margin: 0 0 16px;

  time {
    color: $light-gray;
    margin: 0 33px 0 0;
  }

  a {
    text-decoration: none;
    color: $black-text;;
  }
}

@media (max-width: $tablet-max-width) {
  .info {
    width: 100%;
    max-width: $tablet-max-width;
    box-sizing: border-box;
    margin: 0 auto;
    display: block;
    padding: 0 32px 3px;

    h3 {
      font-family: $Stratos;
      font-weight: normal;
      font-size: 24px;
      line-height: 29px;
      color: $blue;
      margin: 0;
    }

    p {
      font-size: 16px;
      line-height: 22px;
      margin: 19px 0 0;
    }
  }

  .info__bonuses {
    width: 100%;
    max-width: $tablet-max-width;
    margin: 0 auto 60px;

    p {
      width: 100%;
      max-width: 436px;
    }

  }

  .info__bonuses-form {
    margin: 22px auto 0;
    position: relative;

    input {
      width: 576px;
      height: 56px;
      border: 1px solid $button-second;
      box-sizing: border-box;
      border-radius: 8px;
      padding: 26px 0 10px 16px;
    }

    & input ~ label {
      position: absolute;
      top: 18px;
      left: 16px;
      pointer-events: none;
      transition: 0.3s;
    }

    & input:focus ~ label,
    & input:not(:focus):valid ~ label {
      top: 8px;
      font-size: 12px;
      line-height: 14px;
      letter-spacing: 0.02em;
    }
  }

  .info__bonuses-button {
    margin: 16px 0 0 0;
    width: 576px;
    letter-spacing: 0.03em;
    background-color: $button-second;
    color: $blue;
  }

  .info__knowledge {
    width: 100%;
    max-width: $tablet-max-width;
    margin: 0 auto 37px;
  }

  .info__knowledge-list,
  .iinfo__news-list {
    margin: 20px 0 0 2px;
  }

  .info__knowledge-item {
    margin: 0 0 24px;
    width: 100%;

    a {
      font-size: 16px;
      line-height: 22px;
    }
  }

  .info__news {
    width: 100%;
    max-width: $tablet-max-width;
    margin: 0 auto;
  }

  .info__news-item {
    display: flex;
    justify-content: flex-start;
    margin: 0 0 24px;

    time {
      font-size: 12px;
      line-height: 19px;
      margin: 0 33px 0 0;
    }

    a {
      font-size: 16px;
      line-height: 22px;
    }
  }
}

