.up-button {
  position: fixed;
  right: 100px;
  bottom: 30px;
  text-align: center;
  cursor: pointer; }
  .up-button:hover a {
    position: relative;
    color: #2d80fc;
    background-image: url("../img/arrow_up-hover-icon.svg"); }
  .up-button:hover::before {
    position: absolute;
    top: 0;
    left: 6px;
    display: block;
    content: "";
