    height: 48px;
    border-radius: 50%;
    box-shadow: 0px 10px 16px rgba(0, 0, 120, 0.1); }
  .up-button a {
    text-decoration: none;
    margin: 0;
    font-size: 14px;
    line-height: 17px;
    font-weight: bold;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    color: #9999c9;
    padding-top: 60px;
