    width: 60px;
    height: 77px;
    background-image: url("../img/arrow_up-icon.svg");
    background-repeat: no-repeat;
    background-size: 48px 48px;
    background-position: top center; }

.up-button-hide {
  display: none; }

.networks {
  position: fixed;
  right: 6px;
  top: 180px; }

.networks__list {
  list-style: none;
  margin: 0;
  padding: 0; }

.networks__item {
  width: 36px;
  margin: 0 0 5px; }

.networks__item-link svg {
  width: 36px;
  height: 36px;
  fill: #000078; }

.networks__item-link svg:hover {
  fill: #2d80fc; }

.button {
  text-align: center;
  display: block;
  box-sizing: border-box;
  padding: 20px 20px;
  width: fit-content;
  font-size: 14px;
  line-height: 17px;
  font-weight: bold;
  color: #ffffff;
  background-color: #000078;
  border-radius: 8px;
  text-decoration: none;
  text-transform: uppercase; }
  .button:hover {
    background-color: #2d80fc;
    box-shadow: 0 10px 16px rgba(45, 128, 252, 0.2); }

.no-scroll {
  overflow-y: hidden; }

